.main-slider__item{
    position: relative;
    width: 100%;
    background-size: cover;
    img{
        max-width: 100%;
        height: auto;
    }
    &:focus,
    &:active{
        outline: none;
    }
}
.main-slider__title{
    position: absolute;
    top: 40%;
    .inline-block;
    margin-left: 15px;
    text-align: center;
    h3{
        margin-bottom: 30px;
        font-size: 3rem;
        font-weight: normal;
        color: @white;
    }
    p{
        margin-bottom: 40px;
        font-size: 1.8rem;
        color: @white;
    }
    .btn-red{
        font-size: 1.5rem;
        padding: 10px 20px;
    }
}
.slick-dots{
    left: 50%;
    bottom: -1.6%;
    .inline-block;
    margin-left: -124px;
    padding: 10px 25px;
    padding-top: 0;
    padding-bottom: 8px;
    width: auto;
    height: auto;
    background: @white;
    border: 1px solid lightgray;
    border-radius: 0.4%;
    box-shadow: 0 4px 10px -4px rgba(0,0,0,.4);
}
.slick-dots li{
    margin: 0;
    padding: 0;
    width: auto;
    height: auto;
    margin-right: 30px;
}
.slick-dots li:last-child{
    margin-right: 0;
}
.slick-dots li button{
    margin: 0;
    padding: 0;
    width: 8px;
    height: 8px;
    background: gray;
    border-radius: 50%;
    opacity: .7;
}

.slick-dots li button:before{
    content: "";
    width: auto;
    height: auto;
}
.slick-dots li.slick-active button,
.slick-dots li:hover button{
    opacity: 1;
}

/* main-slider-arrows
==========================================*/

.slick-prev,
.slick-next{
    width: 50px;
    height: 48px;
    z-index: 1003;
    background: url(../img/main-slider-arrows.png) no-repeat 0 0;
    opacity: .7;
}
.slick-prev{
    left: 25px;
}
.slick-next{
    right: 25px;
    background-position: -50px;
}
.slick-next:before, .slick-prev:before{
    content: "";
}
.slick-next, .slick-next:focus, .slick-next:hover, .slick-prev, .slick-prev:focus, .slick-prev:hover{
    background: url(../img/main-slider-arrows.png) no-repeat;
}
.slick-next, .slick-next:focus, .slick-next:hover{
    background-position:  -50px;
}
.slick-next:hover, 
.slick-prev:hover{
    opacity: 1;
}

@media only screen and (max-width: 1200px) {
    
}


/* Medium Devices, Desktops */

@media only screen and (max-width: 992px) {
   .main-slider__title{
        h3{
            margin-bottom: 20px;
        }
        p{
            margin-bottom: 30px;
        }
        .btn-red{
            padding: 7px 14px;
        }
    }
}


/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
    .main-slider__title{
        h3{
            margin-bottom: 15px;
        }
        p{
            margin-bottom: 20px;
        }
        .btn-red{
            padding: 7px 14px;
        }
    }
    .slick-dots{
        margin-left: -78px;
        padding: 5px 10px;
        padding-bottom: 8px;
        border-radius: 0.4%;
        box-shadow: 0 4px 10px -4px rgba(0,0,0,.4);
    }
    .slick-dots li{
        margin-right: 20px;
    }
    .slick-dots li:last-child{
        margin-right: 0;
    }
    .slick-dots li button{
        width: 6px;
        height: 6px;
        opacity: .7;
    }
    .slick-prev,
    .slick-prev:hover,
    .slick-next,
    .slick-next:hover{
        width: 0;
        height: 0;
    }
    .fa-bars{
        font-size: 12px;
    }   
}


/* Extra Small Devices, Phones */

@media only screen and (max-width: 480px) {
    .main-slider__title{
        h3{
            margin-bottom: 10px;
        }
        p{
            margin-bottom: 20px;
        }
        .btn-red{
            padding: 5px 10px;
        }
    }  
    .slick-dots{
        margin-left: -48px;
        padding: 0 5px;
        padding-bottom: 4px;
        border-radius: 0.4%;
        box-shadow: 0 4px 10px -4px rgba(0,0,0,.4);
    }
    .slick-dots li{
        margin-right: 10px;
    }
    .slick-dots li:last-child{
        margin-right: 0;
    }
}
