@font-face {
  font-family: 'OpenSans';
  src:  url('../../fonts/OpenSans/OpenSans-Regular.eot'),
        url('../../fonts/OpenSans/OpenSans-Regular.eot?#iefix') format('embedded-opentype'),
        url('../../fonts/OpenSans/OpenSans.woff') format('woff'),
        url('../../fonts/OpenSans/OpenSans.ttf') format('truetype'),
        url('../../fonts/OpenSans/OpenSans.svg#OpenSans') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'OpenSans';
  src: url('../../fonts/OpenSans-Bold/OpenSans-Bold.eot');
  src: url('../../fonts/OpenSans-Bold/OpenSans-Bold.woff') format('woff'),
       url('../../fonts/OpenSans-Bold/OpenSans-Bold.ttf') format('truetype'),
       url('../../fonts/OpenSans-Bold/OpenSans-Bold.svg#OpenSans-Bold') format('svg'),
       url('../../fonts/OpenSans-Bold/OpenSans-Bold.eot?#iefix') format('embedded-opentype');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'OpenSans';
  src: url('../../fonts/OpenSans-Light/OpenSans-Light.eot');
  src: url('../../fonts/OpenSans-Light/OpenSans-Light.woff') format('woff'),
       url('../../fonts/OpenSans-Light/OpenSans-Light.ttf') format('truetype'),
       url('../../fonts/OpenSans-Light/OpenSans-Light.svg#OpenSans-Light') format('svg'),
       url('../../fonts/OpenSans-Light/OpenSans-Light.eot?#iefix') format('embedded-opentype');
  font-weight: lighter;
  font-style: normal;
}
@font-face {
  font-family: 'OpenSans';
  src: url('../../fonts/OpenSans-Italic/OpenSans-Italic.eot');
  src: url('../../fonts/OpenSans-Italic/OpenSans-Italic.woff') format('woff'),
       url('../../fonts/OpenSans-Italic/OpenSans-Italic.ttf') format('truetype'),
       url('../../fonts/OpenSans-Italic/OpenSans-Italic.svg#OpenSans-Italic') format('svg'),
       url('../../fonts/OpenSans-Italic/OpenSans-Italic.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: italic;
}

