*{
	padding: 0;
	margin: 0;
}
body{
	min-width: 320px;
	font-family: @openSans;
	font-size: 0.8rem;
	line-height: @line-height;
	color: @black;
	background: @bodyBg;
}
img{
	max-width: 100%;
	height: auto;
}
a{
    font-family: @openSans;  
    font-size: 0.88rem; 
    color: @black;
    text-transform: uppercase;
    text-decoration: none;
    &:focus,
    &:active{
        outline: none;
    }
}
a:hover{
	color: lighten(@black, 60%);
}
h3{
	font-size: 1.5rem;
	font-weight: bold;
}
ul{
	list-style: none;
}
