.relative{
	position: relative;
}
.main-menu{
	position: absolute;
	top: 30px;
	padding: 15px;
	width: 100%;
	color: @white;
	background: #000;
	z-index: 1005;
	box-sizing: border-box;
	.logo{
		position: absolute;
		top: 0;
		left: 15px;
		.img{
			.inline-block;
			width: 116px;
			height: auto;
			img{
				width: 100%;
				height: auto;
			}
		}
	}	
	&__logo-title{
		.inline-block;
		padding: 20px 15px;
		font-size: 1.5rem;
		font-weight: bold;
		text-transform: uppercase;
	}
	&__content{
		margin-left: 305px;
	}
	&__list{
		.inline-block;
		padding-top: 6px;
	}
	&__list li{
		.inline-block;
		margin-right: 30px;
		a{
			color: @white;
			text-transform: capitalize;
			&:hover{
				color: darken(@white,30%);
			}
		}
	}
	&__form{
		position: relative;
		.inline-block;
		float: right;
		input{
			padding: 10px;
			padding-right: 20px;
			width: 310px;
			border-radius: 5px;
			border: none;
			outline: none;
		}
		button{
			position: absolute;
			top: 0;
			right: 10px;
			height: 100%;
			border: none;
			outline: none;
			background: @white;
			border-top-right-radius: 5px;
			border-bottom-right-radius: 5px;
		}
		.fa-search{
			color: gray;
		}
	}
}

/* media
==================================================*/

@media only screen and (max-width: 1200px) {
    .main-menu__form{
    	input{
    		width: 230px;
    	}
    }
    .main-menu{
    	.logo{
    		.img{
    			width: 90px;
    		}
    	}
    }

}


/* Medium Devices, Desktops */

@media only screen and (max-width: 992px) {
    .show-menu-black{
        display: block;
    	background: @black;
        li{
            text-align: center;
        }
    }
   .main-menu{
   		top: 20px;
   		padding: 0;
   		box-sizing: border-box;
	    .fa-bars{
	    	color: @white;
	    }
    	.logo{
    		.img{
    			width: 70px;
    		}
    	}
    	&__logo-title{
    		margin-top: -6px;
    	}
    	&__content{
    		display: none;
    		margin-left: 90px;
    	}
    	&__list li{
    		position: relative;
    		display: block;
    		padding: 10px;
    		z-index: 1007;
    		transition: background .4s;
    	}
    	&__list li:hover{
    		background: lighten(@black, 10%);
    	}
    	&__list{
    		display: block;
    	}
    	&__form{
    		float: none;
    	}
    }
}


/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
	.show-menu{
	    height: 30px;
	}    
	.main-menu{
		top: 15px;
		box-sizing: border-box;
		&__logo-title{
    		margin-top: -12px;
    	}
		.logo{
			left: 10px;
	    		.img{
	    			width: 40px;
	    		}
	    	}
	}
}


/* Extra Small Devices, Phones */

@media only screen and (max-width: 480px) {	
	.main-menu{
		top: 6px;
		&__logo-title{
    		margin-top: -4px;
    	}
	}
}